

import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { Message } from 'primeng/primeng';
import { ProductGroupBean } from './../bean/product-group.bean';

@Component({
    selector: 'app-group-modify',
    templateUrl: './group-modify.component.html',
    styleUrls: ['./../group-details/group-details.component.scss'],
})
/**
 * 产品组修改
 */
export class GroupModifyComponent implements OnChanges {
    @Input() inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    userform: FormGroup; //校验
    //实体
    productGroupBean: ProductGroupBean = new ProductGroupBean();
    //提示信息
    msgs: Message[] = [];
    iscompele: boolean = false;
    constructor(
        public fb: FormBuilder,
        public ProductManageHttpService: ProductManageHttpService
    ) {
        this.userform = fb.group({
            'groupId': new FormControl('', [Validators.required]),
            'productLineId': new FormControl('', [Validators.required]),
            'groupName': new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
            'groupDsc': new FormControl('', []),
        })
    }

    ngOnInit() {
        this.refresh()
    }
    refresh() {
        this.productGroupBean = Object.assign({}, this.inValue);
    }
    ngOnChanges() {
        this.fuzhi()

    }
    fuzhi() {
        this.productGroupBean = this.inValue;
    }
    //提交
    modifySaveBtn() {
        this.iscompele = true;
        // 验证
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                this.iscompele = false;
                return;
            }
        }

        let parms = {
            productLineId: this.productGroupBean.productLineId,
            productLineName: this.productGroupBean.productLineName,
            groupId: this.productGroupBean.groupId,
            groupName: this.productGroupBean.groupName,
            groupDsc: this.productGroupBean.groupDsc,
        }
        //提交服务
        this.ProductManageHttpService.productCategoryGroupInfoUpdate(parms).subscribe(data => {
            if (data.returnCode.code == "success") {
                let parm = {
                    display: false,
                    code: data.returnCode.message
                }
                this.outValue.emit(parm);
                this.iscompele = false;
            } else {
                this.iscompele = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.iscompele = false;
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        });
    }

    //取消
    modifyCloseBtn() {
        let parm = {
            display: false,
            code: 'cancelModel'
        }
        this.outValue.emit(parm);
    }
}