//产品组-查询 熊正好 2019.3.25
import { Component, OnInit, OnChanges } from '@angular/core';
import { SelectItem } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../http/product-manage.http.service";
import { ProductGroupBean } from './bean/product-group.bean';
import { ConfirmationService } from 'primeng/primeng';//删除
import { Message } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
  selector: 'app-product-group',
  templateUrl: './product-group.component.html',
  styleUrls: ['./product-group.component.scss'],
  providers: [ProductManageHttpService, ConfirmationService, Commfunc]
})
/**
 * 产品组
 */
export class ProductGroupComponent implements OnInit {
  tabMesg: any = tableMessage;//列表无数据
  // bean
  productGroupBean: ProductGroupBean = new ProductGroupBean();

  select_1: any = [];//默认下拉
  all_title: string = '产品组维护';
  //修改的参数
  updateValue: any = [];
  //列表
  cars_2: any;
  arr_2: any;
  //提示信息
  msgs: Message[] = [];
  //总页数
  total: string;
  //每页多少条
  pageSize: string = '10';
  //分页控制
  first: number = 0;
  //模态框
  showModel: string;
  display: boolean = false;
  displayA: boolean = false;
  //模态框的标题
  headerTitle: string;

  line_value: string;//产品线下拉

  constructor(
    public ProductManageHttpService: ProductManageHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {
  }
  ngOnInit() {
    this.select_1 = [
      { label: '请选择', value: "" },
    ];
    this.arr_2 = [
      {
        ch: '产品组编号',
        label: 'groupId',
        isShow: true
      },
      {
        ch: '产品组名称',
        label: 'groupName',
        isShow: true
      },
      {
        ch: '产品线名称',
        label: 'productLineName',
        isShow: true
      },
      {
        ch: '操作时间',
        label: 'modTime',
        isShow: true
      },
    ];
    this.first = 0;
    this.queryClick();
    this.line_query();
    this.productGroupBean.pageSize = 10;
    this.productGroupBean.pageNum = 1;
  }

  // 重置
  resetClick() {
    this.line_value = '';
    this.productGroupBean = new ProductGroupBean()
  }
  // 产品线下拉
  line_query() {
    this.ProductManageHttpService.productLinePull(this.productGroupBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        data.productLineList.forEach(item => {
          let obj = {};
          obj['label'] = item.categoryName;
          obj['value'] = item.productCategoryId;
          this.select_1.push(obj)
        });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  //查询始终第一页
  querySecle() {
    this.productGroupBean.pageSize = 10;
    this.productGroupBean.pageNum = 1;
    this.first = 0;
    this.queryClick();
  }
  //  查询
  queryClick() {
    this.productGroupBean.productLineId = this.line_value;
    this.ProductManageHttpService.productCategoryGroupInfoQueryList(this.productGroupBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.cars_2 = data.productGroupInfoList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //删除
  deleteClick(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = { groupId: item.groupId };
        //提交服务
        this.ProductManageHttpService.productCategoryGroupInfoDelete(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.first = 0;
            this.queryClick();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }
  //新增
  addClick() {
    this.display = true;
    this.headerTitle = '新增';
    this.showModel = '1';
  }
  //新增的回调
  addCall(param) {
    if (param.code == 'cancelModel') {
      this.display = param.display;
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
      this.display = param.display;
    }
    this.first = 0;
    this.queryClick();
  }
  // 修改按钮
  modifyClick(item) {
    this.display = true;
    this.headerTitle = '修改';
    this.showModel = '2';
    // this.updateValue = item.groupId;
    this.updateValue = item;
  }
  //修改的回调
  updateCall(param) {
    if (param.code == 'cancelModel') {
      this.display = param.display;
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
      this.display = param.display;
    }
    this.first = 0;
    this.queryClick();
  }
  // 详情
  detailsClick(item) {
    this.displayA = true;
    this.headerTitle = '产品组详情';
    this.updateValue = item;
    this.showModel = '3';
  }
  //详情的回调
  detailsCall(param) {
    this.displayA = param;
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.productGroupBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.productGroupBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.productGroupBean.pageSize;
    //调用查询的方法
    this.queryClick();
    this.productGroupBean.pageNum = 1;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
