import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { ProductGroupBean } from './../bean/product-group.bean';
import { Message } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import * as _ from 'lodash';

@Component({
  selector: 'app-group-details',
  templateUrl: './group-details.component.html',
  styleUrls: ['./group-details.component.scss',]
})
/**
 * 
 */
export class GroupDetailsComponent implements OnInit {
  tabMesg: any = tableMessage;//列表无数据
  @Input() inValue: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  //实体
  productGroupBean: ProductGroupBean = new ProductGroupBean();
  head_one_title: string = '产品组详情';
  head_two_title: string = '产品组下基础产品';
  //列表
  cars_2: any;
  arr_2: any;
  list: any = [];
  UserRecordInfo: any = {};
  // 分页
  //总页数
  total: number;
  //   pageSize: number;//每页显示数量
  //   pageNum: number;    //当前页

  //提示信息
  msgs: Message[] = [];
  cars_2List: any = [];
  constructor(public ProductManageHttpService: ProductManageHttpService) { }

  ngOnInit() {
    this.arr_2 = [
      {
        ch: '基础产品编号',
        label: 'tplId',
        isShow: true
      },
      {
        ch: '基础产品名称',
        label: 'tplName',
        isShow: true
      },
    ];
    this.UserRecordInfo = this.inValue;
  }
  ngOnChanges() {
    this.productGroupBean = new ProductGroupBean();
    this.queryEvent();
  }
  // 关闭
  closeClick() {
    this.outValue.emit(false);
  }
  queryEvent() {
    let param = {
      groupId: this.inValue.groupId,
      pageSize: this.productGroupBean.pageSize,
      pageNum: this.productGroupBean.pageNum
    };
    this.ProductManageHttpService.productGroupDetailsQueryList(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        // this.total = data.total;
        this.list = data;
        // this.UserRecordInfo = data.recordInfo;
        let Time1 = new Date(data.modTime);
        let Time11 = Time1.toISOString();
        this.list.modTime = Time11.slice(0, 10);
        // this.cars_2 = data.groupTempList;
        // this.cars_2 = _.clone(this.cars_2);
        this.cars_2List = data.groupTempList;
        if (data.groupTempList.length > 0) {
          this.total = data.groupTempList.length;
        } else {
          this.total = 0;
        }
        let currentPages1 = this.productGroupBean.pageNum * 10;
        if (this.cars_2List.length > currentPages1) {
          let currentPages2 = currentPages1 - 10;
          this.cars_2 = this.cars_2List.slice(currentPages2, currentPages1);
        } else {
          let currentPages2 = currentPages1 - 10;
          this.cars_2 = this.cars_2List.slice(currentPages2);
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.productGroupBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.productGroupBean.pageNum = currentPage;
    //调用查询的方法
    this.queryEvent();
    // this.productGroupBean.pageNum = 1;
  }
}
