import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { ProductLineBean } from './../bean/product-line.bean';
import { Message } from 'primeng/primeng';

@Component({
  selector: 'product-line-add',
  templateUrl: './product-line-add.component.html',
  styleUrls: ['./../product-line-details/product-line-details.component.scss'],
  providers: [ProductManageHttpService]
})
/**
 * 产品线管理新增
 */
export class ProductLineAddComponent implements OnInit {

  @Output() outValue: EventEmitter<any> = new EventEmitter();

  userform: FormGroup; //校验
  //实体
  isComplete: boolean = false;
  //提示信息
  msgs: Message[] = [];
  add_title: string = '产品线管理新增';
  productLineIdhead: string = 'L';

  productLineBean: ProductLineBean = new ProductLineBean();

  constructor(
    public productManageHttpService: ProductManageHttpService,
    public fb: FormBuilder,
  ) {
    this.userform = fb.group({
      'productLineName': new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
      'productLineId': new FormControl('', [Validators.pattern(/^[0-9]\d*$/), Validators.required]),
    });
  }

  ngOnInit() {
    this.getSeqNo();
  }
  getSeqNo() {
    let parms = {
      seqType: 'PRODUCT_LINE',
    };
    this.productManageHttpService.getSeqNo(parms).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.productLineBean.productLineId = data.seqNo;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  // 保存
  preserveClick() {
    this.isComplete = true;
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        this.isComplete = false;
        return;
      }
    }
    //提交服务
    //拼接产品线编号
    let parms = {
      productLineId: this.productLineIdhead + this.productLineBean.productLineId,
      productLineName: this.productLineBean.productLineName,
      productLineDsc: this.productLineBean.productLineDsc
    }
    this.productManageHttpService.productCategoryLineAdd(parms).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
        this.productLineBean = new ProductLineBean();
        let parm = {
          display: false,
          code: data.returnCode.message
        }
        this.outValue.emit(parm);
        this.isComplete = false;
      } else if (data.returnCode.code == "05083") {
        this.isComplete = false;
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '产品线编号重复' });
      }
      else {
        this.isComplete = false;
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.isComplete = false;
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  //取消
  addCloseBtn() {
    let parm = {
      display: false,
      code: 'cancelModel'
    }
    this.outValue.emit(parm);
  }

} 
