import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
// 页面
import { ProductLineComponent } from './product-line/product-line.component'; //// 产品层次管理-产品线管理
import { ProductGroupComponent } from './product-group/product-group.component'; // 产品层次管理-产品组管理

const routes: Routes = [
    {
        path: 'product-line', component: ProductLineComponent,
    },
    {
        path: 'product-group', component: ProductGroupComponent,
    },
];

export const ProductCatalogsModuleRouting: ModuleWithProviders = RouterModule.forChild(routes);
