//产品线-查询 熊正好 2019.3.22
import { Component, OnInit, OnChanges } from '@angular/core';
import { ProductManageHttpService } from "./../../http/product-manage.http.service";
import { ProductLineBean } from './bean/product-line.bean';
import { ConfirmationService } from 'primeng/primeng';//删除
import { Message } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
@Component({
  selector: 'app-product-line',
  templateUrl: './product-line.component.html',
  styleUrls: ['./product-line.component.scss',],
  providers: [ProductManageHttpService, ConfirmationService, Commfunc]
})
/**
 * 产品线管理
 */
export class ProductLineComponent implements OnInit {
  first: number = 0;//分页控制
  tabMesg: any = tableMessage;//列表无数据 
  // bean
  productLineBean: ProductLineBean = new ProductLineBean();
  //组件的参数
  updateValue: any = [];
  //提示信息
  msgs: Message[] = [];
  //头部标题
  // all_title: string = '产品管理/产品线管理';
  //模态框
  showModel: string;
  headerTitle: string
  display: boolean = false;
  displayA: boolean = false;
  //总页数
  total: string;

  //列表
  cars_2: any;
  arr_2: any;

  constructor(
    public productManageHttpService: ProductManageHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {

  }

  ngOnInit() {
    this.arr_2 = [
      {
        ch: '产品线编号',
        label: 'productLineId',
        isShow: true
      },
      {
        ch: '产品线名称',
        label: 'productLineName',
        isShow: true
      },
      {
        ch: '产品线描述',
        label: 'productLineDsc',
        isShow: true
      },
    ];
    this.productLineBean.pageSize = 10;
    this.productLineBean.pageNum = 1;
    this.first = 0;
    this.queryClick();
  }

  // 重置
  resetClick() {
    this.productLineBean = new ProductLineBean();
  }
  //查询始终第一页
  querySecle() {
    this.productLineBean.pageSize = 10;
    this.productLineBean.pageNum = 1;
    this.first = 0;
    this.queryClick();
  }
  //  查询
  queryClick() {
    this.productManageHttpService.productCategoryLineQuery(this.productLineBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.cars_2 = data.productLineList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //删除按钮事件
  Test1delete(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        let param = { productLineId: item.productLineId };
        //提交服务
        this.productManageHttpService.productCategoryLineDelete(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.first = 0;
            this.queryClick();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }
  //新增
  Test1add() {
    this.display = true;
    this.headerTitle = '产品线新增';
    this.showModel = '1';
  }
  //新增的回调
  addCall(param) {
    if (param.code == 'cancelModel') {
      this.display = param.display;
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
      this.display = param.display;
    }
    this.first = 0;
    this.queryClick();
  }

  // 修改按钮
  Test1change(item) {
    // this.TestchangeModal=true;
    this.display = true;
    this.headerTitle = '产品线修改';
    this.showModel = '2';
    this.updateValue = item;
  }
  //修改的回调
  updateCall(param) {
    this.productLineBean = new ProductLineBean();
    if (param.code == 'cancelModel') {
      this.display = param.display;
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
      this.display = param.display;
    }
    this.first = 0;
    this.queryClick();
  }
  // 详情
  detailsClick(item) {
    this.displayA = true;
    this.headerTitle = '产品线详情';
    this.updateValue = item.productLineId;
    this.showModel = '3';
  }
  //详情的回调
  detailsCall(param) {
    this.displayA = param;
    // this.queryClick();
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.productLineBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.productLineBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.productLineBean.pageSize;
    //调用查询的方法
    this.queryClick();
    this.productLineBean.pageNum = 1;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
