
import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { ProductGroupBean } from './../bean/product-group.bean';
import { SelectItem, Message } from 'primeng/primeng';

@Component({
  selector: 'app-group-add',
  templateUrl: './group-add.component.html',
  styleUrls: ['./../group-details/group-details.component.scss'],
})
/**
 * // 产品组管理-产品组新增
 */
export class GroupAddComponent implements OnInit, OnChanges {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;
  select_1: any = []; // 默认下拉产品线编号
  userform: FormGroup; // 校验
  select_value: any; // 产品线编号
  groupIdhead: any; // 产品线编号
  groupId: any; // 产品线编号
  iscompele: boolean = false;
  // 实体
  productGroupBean: ProductGroupBean = new ProductGroupBean();

  // 提示信息
  msgs: Message[] = [];
  productLineId_value: any;
  constructor(
    public fb: FormBuilder,
    public ProductManageHttpService: ProductManageHttpService
  ) {
    this.userform = fb.group({
      'select_value': new FormControl('', [Validators.required]),
      'groupId': new FormControl('', [Validators.pattern(/^[0-9]\d*$/), Validators.required]),
      'groupName': new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
      'groupDsc': new FormControl('', []),
    });
  }

  ngOnInit() {
    this.select_1 = [
      { label: '请选择', value: "" },
    ];
  }
  ngOnChanges() {
    this.line_query()
  }
  // 获取默认编码
  getSeqNo() {
    let parms = {
      seqType: 'PRODUCT_GROUP',
      productLineId: this.productLineId_value,
    };
    this.ProductManageHttpService.getSeqNo(parms).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.groupId = data.seqNo;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  // 获取产品线
  line_query() {
    this.productGroupBean = new ProductGroupBean();
    this.ProductManageHttpService.productLinePull(this.productGroupBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        data.productLineList.forEach(item => {
          let obj = {};
          obj['label'] = item.categoryName;
          obj['value'] = item.productCategoryId;
          this.select_1.push(obj)
        });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  change(event) {
    if (this.select_value) {
      this.groupIdhead = 'G' + this.select_value.slice(1, 3);
    }
    this.productLineId_value = event.value;
    if (this.productLineId_value) {
      this.getSeqNo();
    }
  }
  // 添加
  addSaveBtn() {
    this.iscompele = true;
    // 验证
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    // 处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        this.iscompele = false;
        return;
      }
    }
    // 提交服务
    this.productGroupBean.groupId = this.groupIdhead + this.groupId;
    this.productGroupBean.productLineId = this.select_value;
    this.ProductManageHttpService.productCategoryGroupInfoAdd(this.productGroupBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.productGroupBean = new ProductGroupBean();
        let parm = {
          display: false,
          code: data.returnCode.message
        };
        this.outValue.emit(parm);
        this.iscompele = false;
      } else if (data.returnCode.code == "05085") {
        this.iscompele = false;
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '产品线编号重复' });
      } else {
        this.iscompele = false;
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.iscompele = false;
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })

  }
  // 取消
  addCloseBtn() {
    let parm = {
      display: false,
      code: 'cancelModel'
    };
    this.outValue.emit(parm);
  }
}
