
import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { Message } from 'primeng/primeng';
import { ProductLineBean } from './../bean/product-line.bean';

@Component({
    selector: 'product-line-change',
    templateUrl: './product-line-change.component.html',
    styleUrls: ['./../product-line-details/product-line-details.component.scss']
})
/**
 * 产品线管理-修改
 */
export class ProductLineChangeComponent implements OnChanges {
    @Input() inValue: any;

    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //提示信息
    msgs: Message[] = [];
    isComplete: Boolean = false;//完成提交
    userform: FormGroup; //校验
    //实体
    productLineBean: ProductLineBean = new ProductLineBean();
    constructor(
        public productManageHttpService: ProductManageHttpService,
        public fb: FormBuilder,
    ) {
        this.userform = this.fb.group({
            'productLineName': new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
        });
    }

    ngOnInit() {
        this.refresh()
    }
    refresh() {
        this.productLineBean = Object.assign({}, this.inValue);
    }
    ngOnChanges() {
        this.fuzhi()
    }
    fuzhi() {
        this.productLineBean = this.inValue;
    }
    //提交
    preserveClick() {
        this.isComplete = true;
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                this.isComplete = false;
                return;
            }
        }
        let parm = {
            productLineId: this.productLineBean.productLineId,
            productLineName: this.productLineBean.productLineName,
            productLineDsc: this.productLineBean.productLineDsc,
        }
        //提交服务
        this.productManageHttpService.productCategoryLineUpdate(parm).subscribe(data => {
            if (data.returnCode.code == "success") {
                let parm = {
                    display: false,
                    code: data.returnCode.message
                }
                this.outValue.emit(parm);
                this.isComplete = false;
            } else {
                this.isComplete = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.isComplete = false;
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        });
    }
    //取消
    modifyCloseBtn() {
        let parm = {
            display: false,
            code: 'cancelModel'
        }
        this.outValue.emit(parm);
        this.productLineBean = new ProductLineBean();
    }
}