import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { ProductLineBean } from './../bean/product-line.bean';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
  selector: 'product-line-details',
  templateUrl: './product-line-details.component.html',
  styleUrls: ['./product-line-details.component.scss'],
  providers: [Commfunc]
})
/**
 * 产品线详情
 */
export class ProductLineDetailsComponent implements OnInit, OnChanges {
  tabMesg: any = tableMessage; // 列表无数据
  @Input() inValue: string;

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  // 实体
  productLineBean: ProductLineBean = new ProductLineBean();
  // 列表
  cars_2: any;
  // 提示信息
  msgs: Message[] = [];
  // 总页数
  total: number;
  // 每页多少条
  pageSize: number = 10;
  pageNum: number = 1;
  list: any = [];
  cars_2List: any = [];
  constructor(public productManageHttpService: ProductManageHttpService,
    public commfunc: Commfunc) { }

  ngOnInit() {

  }
  ngOnChanges() {
    this.list = [];
    this.queryEvent();
  }
  // 关闭
  closeClick() {
    this.outValue.emit(false);
  }
  queryEvent() {
    let param = {
      productLineId: this.inValue,
    };
    this.productManageHttpService.productCategoryLineDetail(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.list = data;
        this.list.productLineCrtTime = data.productLineCrtTime;
        if (data.productLineModTime) {
          this.list.productLineModTime = data.productLineModTime;
        } else {
          this.list.productLineModTime = '';
        }
        if (data.productLineGroupList) {
          this.cars_2 = data.productLineGroupList;
        } else {
          this.cars_2 = [];
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
    this.queryC();
  }
  queryC() {
    let params = {
      productLineId: this.inValue,
      pageSize: this.pageSize,
      pageNum: this.pageNum
    };
    this.productManageHttpService.productCategoryLineGroupQuery(params).subscribe(data => {
      if (data.returnCode.code == "success") {
        // this.cars_2 = data.productLineGroupList;
        // this.total = data.total;
        this.cars_2List = data.productLineGroupList;
        if (data.productLineGroupList.length > 0) {
          this.total = data.productLineGroupList.length;
        } else {
          this.total = 0;
        }
        let currentPages1 = this.pageNum * 10;
        if (this.cars_2List.length > currentPages1) {
          let currentPages2 = currentPages1 - 10;
          this.cars_2 = this.cars_2List.slice(currentPages2, currentPages1);
        } else {
          let currentPages2 = currentPages1 - 10;
          this.cars_2 = this.cars_2List.slice(currentPages2);
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  // 分页
  paginate(event) {
    // 每页显示的条数
    this.pageSize = event.rows;
    // 当前页
    let currentPage = event.page + 1;
    this.pageNum = currentPage;
    // 调用查询的方法
    this.queryC();
    // this.pageNum = 1;
  }
}
